import React, {useEffect, useState} from 'react'
import axios from 'axios'

const SidePanel = () => {
  const [userCategories, setUserCategories] = useState([])
  useEffect(() => {
    myCategories()  
    return () => {}
  }, [])

  const myCategories  = async () => {
    const { data } = await axios.get("/categories/all");//production
    // const { data } = await axios.get(" http://localhost:3000/pages/entries/1/categories/all");   
    await data && setUserCategories(data)
  };

  return (
    <div className="side-panel">
      <input className='panel-input' placeholder='Search Category'></input>
      <div className='panel-display'>
        {userCategories.map((categories, idx) => {
          return (
            <div key={idx} className="panel-items"> {categories.title}</div>
          )
        })}
      </div>





    </div>

  )
}

export default SidePanel